@import url('https://fonts.googleapis.com/css2?family=Noto+Sans+NKo+Unjoined:wght@400;500;600;700&family=Roboto&family=Shippori+Antique+B1&family=Suranna&display=swap');

:root {
  --black: #24110c;
  --white: #F1F0EF;
  --beige: #f0edc2;
  --pink: #ea4887;
  --blue: #2f9dd8;
}

.material-symbols-outlined {
  font-variation-settings:
    'FILL' 0,
    'wght' 400,
    'GRAD' 0,
    'opsz' 24;
  color: var(--black);
  font-size: 5rem;
  cursor: pointer;
  border: 5px solid var(--black);
  border-radius: 50%;
  padding: 0.5rem;
  user-select: none;
  /* margin-left: -1rem; */
}


body {
  height: 100vh;
  height: calc(var(--vh, 1vh) * 100);
  width: 100%;
  overflow: hidden;
  /* overflow-x: scroll; */
}

.App {
  /* text-align: center; */
  height: 100%;
  width: 150%;
  background-color: var(--beige);
  /* transform: translateX(-50vw); */
  color: var(--black);
  transition: all 0.35s ease-in-out;
}

h1,
h2 {
  font-family: 'Shippori Antique B1', sans-serif;
  /* font-weight: 200; */
}

#hero,
#nav,
#contentContainer {
  height: calc(var(--vh, 1vh) * 100);
  width: 50vw;
}

#hero {
  border-right: 5px solid var(--black);
  overflow: hidden;
  padding: 0;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  background-color: var(--beige);
  color: var(--white);
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  top: 0;
  left: 0;
  text-align: center;
}

#logo {
  transform: scale(0.8);
}

#menuButton,
#languageSwitch {
  color: var(--blue);
  border-color: var(--blue);
  margin: 0 40%;
}

#menuButton {
  visibility: hidden;
  position: absolute;
}

.hideOnDesktop {
  visibility: hidden;
}

#nav {
  background-color: var(--beige);
  padding: 0;
  list-style: none;
  font-family: 'Shippori Antique B1', sans-serif;
  font-weight: 300;
  border-right: 5px solid var(--black);
  position: absolute;
  top: 0;
  left: 50vw;
}

.navItem {
  height: 33.34%;
  width: 100%;
  border-top: 5px solid var(--black);
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  font-size: 5rem;
  /* font-weight: 700; */
  color: var(--black);
  user-select: none;
  cursor: pointer;
  transition: all 0.25s linear;
}

.navItem:first-child {
  border: none;
}

#playgroundTab {
  transform: scale(0.5);
}

#etienneMachineTab {
  transform: scale(0.7);
}

#contentContainer {
  position: absolute;
  top: 0;
  left: 100vw;
  padding: 3rem;
  overflow-y: scroll;
  background-color: var(--beige);
  /* padding-bottom: 8rem; */
}

#contentContainer h1 {
  font-size: 5rem;
}

.contentImg {
  width: 60%;
  border: 4px solid var(--black);
  display: block;
  margin: 1.5rem auto;
}

#bandPic {
  width: 100%;
}

#contentContainer p {
  font-size: 1.5rem;
  font-family: 'Roboto', sans-sans-serif;
}

#contentContainer button,
.accordion-button {
  width: 100%;
  /* border-radius: 50px; */
  font-family: 'Shippori Antique B1', sans-serif;
  padding: 0 1rem;
  border: 2px solid var(--black);
  margin: 0 0 1rem 0;
  font-size: 2rem;
  font-weight: 300;
  color: var(--black);
  background-color: var(--beige);
}

#flush-collapseOne,
.accordion-item {
  background-color: var(--beige);
}

/* #flush-collapseOne {
  border-bottom: 2px solid var(--black);
  border-radius: 0;
} */

.blue:hover {
  background-color: #004AAD;
  color: var(--white);
}

.black:hover {
  background-color: var(--black);
  color: var(--beige);
}

.yellow:hover {
  background-color: #FFDE59;
}

.position0 {
  transform: unset;
}

.position1 {
  transform: translateX(0vw);
}

.position2 {
  transform: translateX(-50vw);
}

@media only screen and (max-width: 1300px) {

  .material-symbols-outlined {
    transform: scale(0.7);
  }

  #logo {
    transform: scale(0.5);
  }

  .navItem {
    font-size: 3rem;
  }

  #playgroundTab {
    transform: scale(0.3);
  }

  #etienneMachineTab {
    transform: scale(0.5);
  }

  #contentContainer {
    padding: 1rem;
  }

  #contentContainer h1 {
    font-size: 3rem;
  }

  #contentContainer p {
    font-size: 1rem;
  }

  #contentContainer button {
    font-size: 1.5rem;
  }
}


@media only screen and (max-width: 768px) {

  #hero,
  #nav,
  #contentContainer {
    width: 100vw;
  }

  #hero {
    background-position: center;
  }

  #menuButton {
    visibility: visible;
    position: unset;
  }

  .hideOnDesktop {
    visibility: visible;
  }

  #nav {
    left: 100vw;
  }

  .navItem {
    font-size: 3rem;
    height: 25%;
  }

  #contentContainer {
    left: 200vw;
  }

  .contentImg {
    width: 100%;
  }

  .position1 {
    transform: translateX(-100vw);
  }

  .position2 {
    transform: translateX(-200vw);
  }
}